import noData from '@/components/noData'
import back from '@/components/back'

export default {
    components:{
        noData,
        back
    },
    data(){
        return {
        	show3: false,
        	loading: false,
            user: this.$store.state.userInfo,
            showConfirm: false,
            sendPackage: null,
            search:"",
            inputFlag: false,
            sortList:[{
            	name:"我的服务包"
            },{
            	name:"所属机构"
            },{
            	name:"所属专科"
            }],
            sortIdx:0,
            list:null,
            page:1,
            haveMore:true,
            patient: this.$store.state.patient
        }
    },
    created(){
        this.$store.dispatch('pushservice', null)
        this.$store.dispatch('pushserviceCont',null)
        this.$store.dispatch('pushSaveDiease',null)
        this.$store.dispatch('pushDisease', [])
        this.$store.dispatch('pushserviceBase', null)
        this.$store.dispatch('pushserviceFollowUp',null)
        this.pull(false)
    },
    mounted(){
        this.getMore()
        this.reload()
        this.scrollIos()
    },
    beforeDestroy(){
        window.onscroll = null
    },
    beforeRouteLeave(to,from,next){   //离开
		window.onscroll = null
		if(from.meta.keepAlive) {
			from.meta.scollTopPosition = this.scrollTopDistance
		}
		next()
	},
	beforeRouteEnter(to, from, next) {  //进入前 
		next(vm => {
			vm.getMore()
			if(from.path != vm.$store.state.prevUrl && vm.$store.state.prevUrl != null){
				//刷新页面
				vm.page = 1
				vm.haveMore = true
				vm.show3 = false
	        	vm.loading = false
	            vm.showConfirm = false
	            vm.sendPackage = null
	            vm.search = ""
	            vm.inputFlag = false
				vm.pull(false)
			}else{
				console.log(vm.sortIdx)
				setTimeout(()=>{
		            vm.$refs.el3.scrollTop = to.meta.scollTopPosition;   
		        },1)
			}
		});
	},
    methods:{
    	searchFor(){
    		//搜索
    		/*if(this.inputFlag){
    			return false;
    		}
    		this.page = 1
    		this.list = null
    		this.pull(false)*/
    		this.$router.push("/recom-all-service?key=" + this.search)
    	},
    	sortTab(idx){
    		//选择tab
    		if(idx == 3){
    			return this.$router.push("/recom-all-service")
    		}
    		this.sortIdx = idx
    		//this.search = ""
    		this.page = 1
    		this.list = null
    		this.pull(false)
    	},
    	send(i){
            this.$store.dispatch('pushServiceChoose', i)
            console.log(this.sortIdx)
            if(this.sortIdx == 0){
            	return this.$router.push('/service-details?fromDrSer=1')
            }else{
            	return this.$router.push('/service-details')
            }
            
        },
    	sendDialog(i){
			if(i.PackStatus == 1 && i.Invalid == 0){
			    this.showConfirm = true
			    this.sendPackage = i
			}
			else if(i.Invalid == 1){
			    this.$toast('该服务包已下架')
			}
			else{
			    this.$toast('该服务包尚未通过审核')
			}
		},
    	
    	
        pull(h){
        	if(this.sortIdx == 3){
        		return false;
        	}
        	let _url = ""
        	let parama = {}
        	switch(this.sortIdx) {
				case 0:
					//医生的个人服务包
					_url = 'PlatFormAPI/ServicePackItem/QueryDrPackPageAndDetail'
					parama = {
						ServicePackName:"",//this.search
						DrId:this.user.Data.User.DoctorId,
	                	Invalid:0,//0-上架，1-下架(-1查询所有)
		                PackStatus:1,//服务包状态(0-审核中，1-已审核，2-审核不通过),-1查询所有 如果是患者查询购买服务包赋值已审核
		                PageIndex: this.page,
		                PageSize: 15,
		                IsRecommend:null,//是否可以推荐(0-否，1-是),null查询所有
					}
					break;
				case 1:
					//医生的所属机构服务包
					_url = 'PlatFormAPI/ServicePackItem/QueryOrgAndDrPackPageAndDetail'
					parama = {
						ServicePackName:"",//this.search
						OrgId:this.user.Data.User.OrgId[0] ||-1,
						Invalid:0,//0-上架，1-下架(-1查询所有)
						PackStatus:1,//服务包状态(0-审核中，1-已审核，2-审核不通过),-1查询所有 如果是患者查询购买服务包赋值已审核
		                PageIndex: this.page,
		                PageSize: 15,
		                IsRecommend:1,//是否可以推荐(0-否，1-是),null查询所有
		                DrId:this.user.Data.User.DoctorId,
					}
					break;
				case 2:
					//医生所有专科团队的服务包或者团队成员服务包
					_url = 'PlatFormAPI/ServicePackItem/QueryDrSpecialistHosGroupPackPage'
					parama = {
						ServicePackName:"",//this.search
						DrId:this.user.Data.User.DoctorId,
		                PageIndex: this.page,
		                PageSize: 15,
		                IsRecommend:1,//是否可以推荐(0-否，1-是),null查询所有
					}
					break;
				default:
					break;
			}
            this.$post(_url,parama).then(o => {
            	this.show3 = false
            	this.loading = false
            	o.ReturnData = o.ReturnData || []
                if (!o.ReturnData || o.ReturnData.length < 15){
                    this.haveMore = false
               	}
                else {
                    this.haveMore = true
                }
                    
                if (h){
                    o.ReturnData.forEach(v => {
                        this.list.push(v)
                    })
                }else {
                    this.list = o.ReturnData || []
               	}
            })
        },
        confirm(){
            const i = this.sendPackage
            const pack = {
                "Title": i.ServicePackName,
                "Content":[
                    {
                        "Type":"TextDetail",
                        "ContentElem":{
                            "Text":"服务总价",
                            "Detail": `${i.Price}元`
                        }
                    }
                ],
                "ExtData":{
                    "Type":32,
                    "Data":{
                        "serviceId": i.ServicePackId,
            			"DrId": i.DrId,
            			"DrName":i.DrName,
            			"ReComDrId":this.user.Data.User.DoctorId,
            			"ReComDrName":this.user.Data.User.DoctorName
                    }//
                },
                "IsSystem":false
            }
            i.Details = i.Details || []
            i.Details.forEach(v => {
                if(v.GoodsSource && (v.GoodsSource == 2 || v.GoodsSource == 3)){
	                    pack.Content.push({
	                        "Type":"TextDetail",
	                        "ContentElem":{
	                            "Text": v.ServiceItemName,
	                            "Detail": `x${v.NumValue}`
	                        }
	                    })
	                }
	            	else if(v.SericeItemCode == '1701'){
	                    pack.Content.push({
	                        "Type":"TextDetail",
	                        "ContentElem":{
	                            "Text": v.ServiceItemName,
	                            "Detail": "1份"
	                        }
	                    })
	                }
               	else if(v.NumType != 7  && v.NumType != 8 && v.NumType != 9 && v.NumType != null){
                    pack.Content.push({
                        "Type":"TextDetail",
                        "ContentElem":{
                            "Text": v.ServiceItemName,
                            "Detail": `${v.NumValue}${v.NumTypeName}`
                        }
                    })
                }
                else if(v.NumType == 9 || v.NumType == null){
                    pack.Content.push({
                        "Type":"TextDetail",
                        "ContentElem":{
                            "Text": v.ServiceItemName,
                            "Detail": `${v.TotalPrice}元`
                        }
                    })
                }
                else if(v.NumType == 7 || v.NumType == 8){
                    pack.Content.push({
                        "Type":"TextDetail",
                        "ContentElem":{
                            "Text": v.ServiceItemName,
                            "Detail": `${v.NumTypeName}`
                        }
                    })
                }
            })
            //获取推荐记录id
            this.$post('/PlatFormAPI/ServicePackItem/SavePackRecommendRecord',{
                RecomRecordId: 0,
                DrId: this.user.Data.User.DoctorId,
                DrName:this.user.Data.User.DoctorName,
                ServicePackId:i.ServicePackId,
                ServicePackName:i.ServicePackName,
                PatientId: this.patient.PatientId,
                PatientName: this.patient.PatientName,
            }).then(o => {
            	pack.ExtData.Data.RecomRecordId = o.RecomRecordId
                this.$bridge.callhandler('ServicePack', JSON.stringify(pack))
            	this.$bridge.callhandler('Back')
            })
            /*this.$bridge.callhandler('ServicePack', JSON.stringify(pack))
            this.$bridge.callhandler('Back')*/
        },
        toCreate(){
            if(this.$route.query.fromService){
                this.$store.dispatch('pushDisease', [])
                this.$store.dispatch('pushserviceBase', null)
                this.$store.dispatch('pushserviceCont', null)
                this.$router.push('/create-service-base')
            }
        },
		sendDialog(i){
			if(i.PackStatus == 1 && i.Invalid == 0){
			    this.showConfirm = true
			    this.sendPackage = i
			}
			else if(i.Invalid == 1){
			    this.$toast('该服务包已下架')
			}
			else{
			    this.$toast('该服务包尚未通过审核')
			}
		},
        getMore(){
    		//加载更多
            const el3 = this.$refs.el3
            let timer3 = null
            el3.onscroll = () => {
                clearTimeout(timer3)
                timer3 = setTimeout(() => {
                   const top = el3.scrollTop
                                || el3.pageYOffset
                                || el3.scrollTop,
                            h = el3.scrollHeight,
                            wh = window.innerHeight
                    this.scrollTopDistance = top;
                    if (h - top - wh < -90 && this.haveMore){
                        this.page += 1
                        this.loading = true
                        this.pull(true);
                    }
                }, 100)
            }
        },
        reload(){
            const el2 = this.$refs.el2
            const el3 = this.$refs.el3
            let startX = null
            let startY = null
            let endX = null
            let endY = null
            el2.addEventListener('touchstart', e => {
                this.show3 = false
                startX = e.changedTouches[0].pageX
                startY = e.changedTouches[0].pageY
            })
            el2.addEventListener('touchmove', e => {
                endX = e.changedTouches[0].pageX
                endY = e.changedTouches[0].pageY
                if(Math.abs(startX - endX)<Math.abs(startY-endY) && (startY-endY)<0){
                    const top = el3.scrollTop
                            || el3.pageYOffset
                    if(top < 10 || !top){
                        this.show3 = true
                    }
                } 
            })
            el2.addEventListener('touchend',() => {
                if(this.show3){
                    this.page = 1
                    this.haveMore = true
                    this.pull(false)
                }
            })
        },
        scrollIos(){
			//ios页面卡死问题
			let el3 = this.$refs.el3
			let lastY = 0;
			el3.addEventListener('touchstart', function(event) {
				lastY = event.touches[0].clientY;
			});
			//获取滚动元素
			let scrollEle =this.$refs.el3
			//先移除监听事件
			scrollEle.removeEventListener('touchmove', function (event) {
				event.preventDefault();
			}, false);
			//再添加监听事件
			scrollEle.addEventListener('touchmove', function(event) {
				let top = event.touches[0].clientY;
				let scrollTop = scrollEle.scrollTop;
				let scrollHeight = scrollEle.scrollHeight;
				let clientHeight = scrollEle.clientHeight;
				let direction = (lastY - top) < 0 ? "up" : "down";
				if (scrollTop == 0 && direction == "up") {
				  event.preventDefault();
				} else if (scrollTop >= (scrollHeight - clientHeight) && direction == "down") {
				  event.preventDefault();
				}
				lastY = top;
			});
		  
		}
    }
}



